import React, { useRef } from 'react';
import { AriaListBoxOptions, useListBox } from 'react-aria';
import { ListState } from 'react-stately';
import type { CollectionChildren } from '@react-types/shared';

import Option from './Option';

import styles from './ListBox.module.scss';

export interface ListBoxProps<V extends object> extends AriaListBoxOptions<V> {
    state: ListState<V>;
    children?: CollectionChildren<V>;
}

const ListBox = <V extends object>(props: ListBoxProps<V>) => {
    const ref = useRef(null);
    const { state } = props;
    const { listBoxProps } = useListBox(props, state, ref);

    return (
        <ul className={styles.base} {...listBoxProps} ref={ref}>
            {[...state.collection].map((item) => (
                <Option key={item.key} item={item} state={state} />
            ))}
        </ul>
    );
};

export default ListBox;
